body {
    background-color: #010101;
    font-family: 'Poppins', sans-serif;
}

.app{
    margin-left: 156px;
    margin-top: 24px;
    margin-right: 156px;
}

.home {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.introduction {
    margin-top: 100px;
    margin-bottom: 0;
    width: 80%;
}

.navbar {
    display: flex;
    justify-content: space-between;
}

.name {
    color: #FF0653;
    font-size: 28px;
    line-height: 42px;
    font-weight: 900;
    margin-top: 0;
    margin-bottom: 0;
    font-family: 'Poppins';
}

.section-name-active {
    font-size: 14px;
    color: #FF0653;
    font-weight: 600;
    font-family: 'Work Sans', sans-serif;
    line-height: 21px;
    margin-left: 10px;
    cursor: pointer;
    text-decoration: none;
}

.section-name {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.55);
    font-weight: 600;
    font-family: 'Work Sans', sans-serif;
    line-height: 21px;
    margin-left: 10px;
    cursor: pointer;
    text-decoration: none;
}

.introduction-image {
    max-width: 566px;
    max-height: 572px;
    flex-basis: 40%;
    margin-top: 120px;
}

.download-resume {
    display: flex;
    background-color: #343840;
    border-radius: 24px;
    width: 271px;
    height: 40px;
    font-size: 18px;
    border: none;
    font-family: 'Poppins';
    margin-top: 50px;
}

.projects {
    margin-top: 50px;
}

.projects-header {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    margin-top: 50px;
}

.projects-heading {
    display: flex;
    align-items: center;
    justify-content: center;
}

.description {
    color: #FFFFFF;
    width: 50%;
    font-family: 'Work Sans';
    font-weight: 300;
    text-align: center;
    font-size: 23px;
}

.project {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-bottom: 60px;
}

.project-image-container {
    border-radius: 16px;
    padding-right: 30px;
    padding-left: 30px;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 500px;
    margin: auto;
}

.project-image{
    height: 210px;
    width: 200px;
    padding-right: 30px;
    padding-left: 30px;
    margin: auto;
}

.project-description {
    color: rgba(255, 255, 255, 0.55);
    width: 50%;
    font-size: 18px;
    font-weight: 300;
    text-align: left;
}

.about {
    margin-top:50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.about-description-container {
    display: flex;
    flex-direction: column;
    margin: auto;
}

.about-description-heading {
    display: flex;
    flex-direction: row;
}

.about-images-container {
    display: flex;
    flex-direction: row;
}

.about-images {
    border-radius: 16px;
    height: 300px;
    width: 220px;
    margin-bottom: 60px;
    margin-left: 60px;
}

.about-description {
    color: rgba(255, 255, 255, 0.55);
    width: 70%;
}

.footer-heading {
    font-size: 56px;
    color:rgba(255, 255, 255, 0.55);
}

.footer-description {
    color:rgba(255, 255, 255, 0.44);
    font-size: 18px;
    font-weight: 300;
}

.footer-images {
    margin-top: 30px;
}

.footer-image {
    height: 48px;
    width: 48px;
    margin-left: 15px;
    margin-right: 15px;
}
